import React, {Component} from 'react';

import Appheader from '../app-header';
import SearchPanel from '../search-panel';
import PostStatusFilter from '../post-status-filter';
import PostList from '../post-list';
import PostAddForm from '../post-add-form';

import './app.css';
import '../app-header/app-header.css';
import '../post-add-form/post-add-form.css';
import '../post-list/post-list.css';
import '../post-list-item/post-list-item.css';
import '../search-panel/search-panel.css';

export default class App extends Component {

  state = {
    data: [
      {label: "Going to learn React", important: true, like: true, id: 1},
      {label: "That is good", important: false, like: false, id: 2},
      {label: "I need a break...", important: false, like: false, id: 3}
    ],
    term: '',
    filter: 'all'
  }

  maxId = 4;

  deleteItem = (id) => {
    this.setState(({data}) => {
      const newArr = data.filter(item => item.id !== id);

      return {
        data: newArr
      }
    })
  }

  addItem = (body) => {
    const newItem = {
      label: body,
      important: false,
      like: false,
      id: this.maxId++
    }

    this.setState(({data}) => {
      const newArr = [...data, newItem];

      return {
        data: newArr
      }
    })
  }

  onToggleLike = (id) => {
    this._onToggle(id, 'like');
  }

  onToggleImportant = (id) => {
    this._onToggle(id, 'important');
  }

  _onToggle = (id, property) => {
    this.setState(({data}) => {
      const index = data.findIndex(item => item.id === id);
      const oldItem = data[index];
      const newItem = {...oldItem, [property]: !oldItem[property]};

      const newArr = [...data.slice(0, index), newItem, ...data.slice(index + 1)];

      return {
        data: newArr
      }
    })
  }

  onUpdateSearch = (term) => {
    this.setState({term});
  }

  onFilterSelect = (filter) => {
    this.setState({filter});
  }

  searchPosts = (items, term) => {
    if (term.length === 0) {
      return items;
    }

    return items.filter((item) => {
      return item.label.indexOf(term) > -1;
    });
  }

  filterPosts = (items, filter) => {
    if (filter === 'like') {
      return items.filter(item => item.like);
    } else {
      return items;
    }
  }

  render() {
    const {data, term, filter} = this.state;
    const allPosts = data.length;
    const likePosts = data.filter(item => item.like).length;
    const visiblePosts = this.filterPosts(this.searchPosts(data, term), filter);

    return (
      <div className="app">
          <Appheader
            allPosts={allPosts}
            likePosts={likePosts}/>
        <div className="search-panel d-flex">
          <SearchPanel
            onUpdateSearch={this.onUpdateSearch}/>
          <PostStatusFilter
            filter={filter}
            onFilterSelect={this.onFilterSelect}/>
        </div>
          <PostList
            onDelete={this.deleteItem}
            onToggleImportant={this.onToggleImportant}
            onToggleLike={this.onToggleLike}
            posts={visiblePosts}/>
          <PostAddForm
            onAdd={this.addItem}/>
      </div>
    )
  }
}
