import React, {Component} from 'react';

export default class App extends Component {

  render() {
    const {allPosts, likePosts} = this.props;

    return (
      <div className="app-header d-flex">
        <h1>Bludov Dmytro</h1>
        <h2>{allPosts} posts, liked {likePosts}</h2>
      </div>
    )
  }
}
